.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.map {
  width: 500px;
  height: 300px;
  
}

header img {
  max-width: 136px;
}

header {
  position: fixed;
  height: 55px;
  padding: 6px 15px 7px;
  margin-top: 0px;
  margin-bottom: 0px;
  top: 0px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  background: white;
  border-bottom: 1px solid #413a2e50;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.button-primary {
  margin: 8px;
  text-decoration: none;
  font-family: "Stolzl", Sans-serif;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 17.6px;
  letter-spacing: -0.48px;
  fill: #ffffff;
  color: #ffffff;
  background-color: #969aff;
  border-style: solid;
  border-width: 2px 2px 2px 2px;
  border-color: #969aff;
  border-radius: 200px 200px 200px 200px;
  padding: 11px 24px 10px 24px;
}

.button-primary:hover {
  color: #969aff;
  background-color: #ffffff;
}

.button-secondary {
  margin: 8px;
  text-decoration: none;
  font-family: "Stolzl", Sans-serif;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 17.6px;
  letter-spacing: -0.48px;
  fill: #ffffff;
  color: #413a2e;
  background-color: #ffffff;
  border-style: solid;
  border-width: 2px 2px 2px 2px;
  border-color: #969aff;
  border-radius: 200px 200px 200px 200px;
  padding: 11px 24px 10px 24px;
}

.button-secondary:hover,
.button-secondary.active {
  color: #ffffff;
  background-color: #969aff;
}

.header-buttons-container {
  padding: 0 20px;
}

.background-image-1 {
  position: absolute;
  z-index: -1;
  position: absolute;
  z-index: -1;
  top: 300px;
  right: 0px;
  overflow: hidden;
  opacity: 0.8;
}

.background-image-2 {
  position: absolute;
  z-index: -1;
  position: absolute;
  z-index: -1;
  left: 0px;
  top: 200px;
  overflow: hidden;
}

.button-group > button {
  margin: 0;
}

.button-group > button:first-child {
  margin: 0;
  border-radius: 200px 0 0 200px;
  border-right-width: 0;
}
.button-group > button {
  margin: 0;
  border-radius: 0;
  border-right-width: 0;
  border-width: 1px; 
  border-color: #413a2e40;
}
.button-group > button:last-child {
  margin: 0;
  border-radius: 0 200px 200px 0;
  border-right-width: 1px;
}

.section {
    display: flex;
    justify-content: center;
    align-items: center;
}